body{font:200 1em Helvetica,sans-serif}h2{margin:0 0 14px;font-size:1.4em}ul{padding:0 0 0 24px}ul li{position:relative;margin:8px 0;list-style:none}ul li:before{position:absolute;top:2px;bottom:3px;left:-8px;width:3px;content:"";background:#333;border-radius:3px}.milestone{display:flex}.milestone--complete .milestone__content{opacity:1}.milestone__timeline{display:flex;justify-content:flex-start;width:20px;padding:0 0 0 10px}.milestone__timeline-point{display:flex;width:14px;padding:4px 2px 0}.milestone__timeline-point:after{width:14px;height:3px;content:"";background:#c7c7c7;border-radius:10px}.milestone__content{margin:-2px 0 0;padding:0 20px 20px;opacity:.5}.milestone__title{margin:0}.milestone__version{font-size:.8em;font-weight:600}.timeline-bar{position:relative;display:flex;align-self:stretch;width:10px;background:#e0e0e0}.timeline-bar--background-pending{background:#9ad89a}.timeline-bar--background-complete{background:#00b100}.timeline-bar--background-rounded-start{border-top-left-radius:5px;border-top-right-radius:5px}.timeline-bar--background-rounded-end{border-bottom-left-radius:5px;border-bottom-right-radius:5px}.timeline-bar--bar-rounded-start .timeline-bar__content{border-top-left-radius:5px;border-top-right-radius:5px}.timeline-bar--bar-rounded-end .timeline-bar__content{border-bottom-left-radius:5px;border-bottom-right-radius:5px}.timeline-bar--bar-complete .timeline-bar__content,.timeline-bar--bar-pending .timeline-bar__content{display:block}.timeline-bar--bar-pending .timeline-bar__content{background:#9ad89a}.timeline-bar--bar-complete .timeline-bar__content{background:#00b100}.timeline-bar--bar-peak-highlight .timeline-bar__content{background:#00d30e;background:linear-gradient(180deg,#00d30e 0,#4ad300 12%,#00c109 36%,#00b100);filter:progid:DXImageTransform.Microsoft.gradient(startColorstr="#00d30e",endColorstr="#00b100",GradientType=0)}.timeline-bar__content{position:absolute;display:none;right:0;bottom:0;left:0;height:0}
